body {
  margin: 0;
  padding: 0;
}


#root {
  height: 100%;
  width: 100%;
}
#drum-machine {
  -webkit-box-shadow: 1px 1px 5px 5px rgb(0,0,0);
          box-shadow: 1px 1px 5px 5px rgb(0,0,0);
  height: 300px;
  width: 320px;
  border: 1px solid black;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(8,1fr);
  grid-template-rows: repeat(8,1fr);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

#display {
  grid-row: 1/4;
  grid-column: 2/8;
  background: #00FF00;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

#powerwrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-size: 10px;
  -ms-flex-pack: stretch;
      justify-content: stretch;
  -ms-flex-align: center;
      align-items: center;
  grid-row: 1/4;
  grid-column: 1/1;
}

#powerwrapper p {
  -ms-flex: 0 1;
      flex: 0 1;
  margin: 5px;
}

#powerslider {
  border: 1px solid gray;
  -ms-flex: 1 1;
      flex: 1 1;
  width: 10px;
}

#volumewrapper {
  grid-row: 1/4;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: stretch;
      justify-content: stretch;
  -ms-flex-align: center;
      align-items: center;
  font-size: 10px;
}

#volumeslider {
  border: 1px solid gray;
  width: 10px;
  -ms-flex: 1 1;
      flex: 1 1;
}

#volumewrapper p {
  margin: 5px;
}

#keys { 
  grid-row: 4/9;
  grid-column: 2/8;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

#keys div {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid green;
  width: calc(100%/5);
  margin: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

#volumeslider::after {
  content: "█"; 
  display: -ms-flexbox; 
  display: flex;
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: end;
      align-items: flex-end;
}

#powerslider::after{
  content:"█";
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: end;
      align-items: flex-end;
}


@media screen and (min-width:800px){
  #keys{
    grid-column: 1/9;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-pack: stretch;
        justify-content: stretch;
    -ms-flex-align: stretch;
        align-items: stretch;
  }
  #keys div {
    width: calc(100%/9);
    margin: 5px;;
    -webkit-box-shadow: 1px 1px 1px 5px rgb(0,0,0);;
            box-shadow: 1px 1px 1px 5px rgb(0,0,0);
  }
  #keys div:first-of-type{
    margin-left: 0;
  }
  
  #keys div:last-of-type {
    margin-right: 0;
  }
}


